import {toDescriptor} from "../TransformDescriptor.js";
import {asPages} from "./Page.js";
import ChangeTracker from "./ChangeTracker.js";
import ItemGroup from "./ItemGroup.js";
import EvaluationTracker from "./EvaluationTracker.js";
import Globals from "../Globals.js";
export default class StageResult {
  constructor(globals, descriptor, schema, pages, evaluations, changes, messages) {
    this.globals = globals;
    this.descriptor = descriptor;
    this.schema = schema;
    this.pages = pages;
    this.evaluations = evaluations;
    this.changes = changes;
    this.messages = messages;
  }
  itemsUnpacked() {
    return this.pages.reduce((items, page) => {
      page.itemGroups.forEach((itemGroup) => itemGroup.unpacked().forEach((item) => items.push(item)));
      return items;
    }, []);
  }
  itemsCleanedAndUnpacked() {
    return this.pages.reduce((items, page) => {
      page.itemGroups.forEach((itemGroup) => itemGroup.unpacked().filter((item) => !this.changes.isRemoved(item)).forEach((item) => items.push(item)));
      return items;
    }, []);
  }
  selectPages(relevantChangesOnly, groupItems) {
    let result;
    if (!groupItems && this.descriptor?.debug?.itemMerger) {
      result = this.pagesWithUnpackedItems();
    } else {
      result = this.pages;
    }
    if (relevantChangesOnly && !this.descriptor.debug?.showAll === true) {
      result = result.map((page) => ({
        ...page,
        itemGroups: page.itemGroups.filter((itemGroup) => this.evaluations.evaluated(itemGroup.top) || this.changes.hasChanged(itemGroup.top))
      }));
    }
    return result;
  }
  pagesWithUnpackedItems() {
    return this.pages.map((page) => ({
      ...page,
      itemGroups: new Array().concat(...page.itemGroups.map((itemGroup) => itemGroup.unpacked().map((item) => new ItemGroup(item))))
    }));
  }
}
export function initialStage(inputSchema, inputItems) {
  const schema = inputSchema.map((column) => ({name: column}));
  const evaluations = new EvaluationTracker();
  const changes = new ChangeTracker();
  const pages = asPages(evaluations, changes, inputItems);
  const messages = [
    `Parsed ${inputItems.length === 0 ? 0 : inputItems[inputItems.length - 1].page + 1} pages with ${inputItems.length} items`
  ];
  return new StageResult(new Globals(), toDescriptor({debug: {showAll: true}}), schema, pages, evaluations, changes, messages);
}
