import ItemTransformer from "./ItemTransformer.js";
export default class NoOpTransformer extends ItemTransformer {
  constructor() {
    super("Does nothing", "Simply for displaying the results.", {
      debug: {
        showAll: true
      }
    });
  }
  transform(_, inputItems) {
    return {
      items: inputItems,
      messages: []
    };
  }
}
