export function flatMap(array, func) {
  return array.reduce((result, entry, idx) => result.concat(func(entry, idx)), []);
}
export function onlyUniques(value, index, self) {
  return self.indexOf(value) === index;
}
export function ascending(a, b) {
  return a - b;
}
export function count(array, find) {
  return array.reduce((count2, entry) => find(entry) ? count2 + 1 : count2, 0);
}
export function median(values) {
  if (values.length === 0)
    return 0;
  values.sort(function(a, b) {
    return a - b;
  });
  var half = Math.floor(values.length / 2);
  if (values.length % 2)
    return values[half];
  return (values[half - 1] + values[half]) / 2;
}
function groupBy(items, extractKey) {
  return items.reduce((pageItems, item) => {
    const lastPageItems = pageItems[pageItems.length - 1];
    if (!lastPageItems || extractKey(item) !== extractKey(lastPageItems[0])) {
      pageItems.push([item]);
    } else {
      lastPageItems.push(item);
    }
    return pageItems;
  }, []);
}
export function groupByPage(items) {
  return groupBy(items, (item) => item.page);
}
export function groupByLine(items) {
  return groupByElement(items, "line");
}
export function groupByElement(items, elementName) {
  return groupBy(items, (item) => item.data[elementName]);
}
export function transformGroupedByPage(items, groupedTransformer) {
  return new Array().concat(...groupByPage(items).map((pageItems) => groupedTransformer(pageItems[0].page, pageItems)));
}
export function transformGroupedByPageAndLine(items, groupedTransformer) {
  let transformedItems = [];
  groupByPage(items).forEach((pageItems) => {
    groupByElement(pageItems, "line").forEach((lineItems) => {
      transformedItems.push(...groupedTransformer(pageItems[0].page, lineItems[0].data["line"], lineItems));
    });
  });
  return transformedItems;
}
export function mostFrequent(items, dataElementKey) {
  const occurenceMap = items.reduce((map, item) => {
    const key = item.data[dataElementKey];
    const occurrence = map.get(key) || 0;
    map.set(key, occurrence + 1);
    return map;
  }, new Map());
  const topElement = [...occurenceMap].reduce((topEntry, entry) => entry[1] >= topEntry[1] ? entry : topEntry, [void 0, 0])[0];
  return topElement;
}
export function majorityElement(items, extract) {
  if (items.length == 0) {
    return;
  }
  let maj = 0, count2 = 1;
  for (let i = 1; i < items.length; i++) {
    if (extract(items[i]) === extract(items[maj])) {
      count2++;
    } else {
      count2--;
    }
    if (count2 === 0) {
      maj = i;
      count2 = 1;
    }
  }
  return extract(items[maj]);
}
